import * as THREE from "three";
import { shaderStructs, shaderIntersectFunction, MeshBVHUniformStruct } from 'three-mesh-bvh';

// console.log("intersect function", shaderIntersectFunction)

const vertexShaderAssignments = `#ifdef USE_INSTANCING
      vModelMatrixInverse = inverse(modelMatrix * instanceMatrix);
    #else
      vModelMatrixInverse = inverse(modelMatrix);
    #endif
    //vModelMatrix = modelMatrix;
    vec4 transformedNormal_d = vec4(normal, 0.0);
    vec4 transformedPosition = vec4(position, 1.0);
    vWorldPosition = (modelMatrix * transformedPosition).xyz;
    vNormalInverse = normalize((viewMatrixInverse * vec4(normalMatrix * transformedNormal_d.xyz, 0.0)).xyz);`;

const vertexShaderVaryings = `uniform mat4 viewMatrixInverse;

#ifndef USE_TRANSMISSION
  varying vec3 vWorldPosition;
#endif
  varying vec3 vNormalInverse;
  varying mat4 vModelMatrixInverse;
  varying mat4 vModelMatrix;

  #ifdef USE_INSTANCING_COLOR
    varying vec3 vInstanceColor;
  #endif`;

const vertexShader = `
    vec4 transformedNormal = vec4(normal, 0.0);
    vec4 transformedPosition = vec4(position, 1.0);
    #ifdef USE_INSTANCING
      transformedNormal = instanceMatrix * transformedNormal;
      transformedPosition = instanceMatrix * transformedPosition;
    #endif

    #ifdef USE_INSTANCING
      vModelMatrixInverse = inverse(modelMatrix * instanceMatrix);
    #else
      vModelMatrixInverse = inverse(modelMatrix);
    #endif

    #ifdef USE_INSTANCING_COLOR
      vInstanceColor = instanceColor.rgb;
    #endif

    vWorldPosition = (modelMatrix * transformedPosition).xyz;
    vNormal = normalize((viewMatrixInverse * vec4(normalMatrix * transformedNormal.xyz, 0.0)).xyz);
    gl_Position = projectionMatrix * viewMatrix * modelMatrix * transformedPosition;`;

const fragmentShader = `//#define ENVMAP_TYPE_CUBE_UV
//   precision highp isampler2D;
//   precision highp usampler2D;
#ifndef USE_TRANSMISSION
  varying vec3 vWorldPosition;
#endif
  varying vec3 vNormalInverse;
  varying mat4 vModelMatrixInverse;
  varying mat4 vModelMatrix;

  #ifdef USE_INSTANCING_COLOR
    varying vec3 vInstanceColor;
  #endif

  #ifdef ENVMAP_TYPE_CUBEM
    uniform samplerCube envMapD;
  #else
    uniform sampler2D envMapD;
  #endif

  uniform float bounces;
  ${shaderStructs}
  ${shaderIntersectFunction}
  uniform BVH bvh;
//   uniform float ior;
  uniform bool correctMips;
  uniform vec2 resolution;
  uniform float fresnel;
  #ifndef USE_TRANSMISSION
  uniform mat4 modelMatrix;
  #endif
  uniform mat4 projectionMatrixInverse;
  uniform mat4 viewMatrixInverse;
  uniform float aberrationStrength;
  uniform vec3 color;

  float fresnelFunc(vec3 viewDirection, vec3 worldNormal) {
    return pow( 1.0 + dot( viewDirection, worldNormal), 10.0 );
  }

  vec3 totalInternalReflection(vec3 ro, vec3 rd, vec3 normal, float ior, mat4 modelMatrixInverse) {
    vec3 rayOrigin = ro;
    vec3 rayDirection = rd;
    rayDirection = refract(rayDirection, normal, 1.0 / ior);
    rayOrigin = vWorldPosition + rayDirection * 0.001;
    rayOrigin = (modelMatrixInverse * vec4(rayOrigin, 1.0)).xyz;
    rayDirection = normalize((modelMatrixInverse * vec4(rayDirection, 0.0)).xyz);
    for(float i = 0.0; i < bounces; i++) {
      uvec4 faceIndices = uvec4( 0u );
      vec3 faceNormal = vec3( 0.0, 0.0, 1.0 );
      vec3 barycoord = vec3( 0.0 );
      float side = 1.0;
      float dist = 0.0;
      bvhIntersectFirstHit( bvh, rayOrigin, rayDirection, faceIndices, faceNormal, barycoord, side, dist );
      vec3 hitPos = rayOrigin + rayDirection * max(dist - 0.001, 0.0);
      vec3 tempDir = refract(rayDirection, faceNormal, ior);
      if (length(tempDir) != 0.0) {
        rayDirection = tempDir;
        break;
      }
      rayDirection = reflect(rayDirection, faceNormal);
      rayOrigin = hitPos + rayDirection * 0.01;
    }
    rayDirection = normalize((modelMatrix * vec4(rayDirection, 0.0)).xyz);
    // rayDirection = normalize((vModelMatrix * vec4(rayDirection, 0.0)).xyz);
    return rayDirection;
  }

//    #include <common>
//   #include <cube_uv_reflection_fragment>

  #ifdef ENVMAP_TYPE_CUBEM
    vec4 textureGradient(samplerCube envMap, vec3 rayDirection, vec3 directionCamPerfect) {
      return textureGrad(envMap, rayDirection, dFdx(correctMips ? directionCamPerfect: rayDirection), dFdy(correctMips ? directionCamPerfect: rayDirection));
    }
  #else
    vec4 textureGradient(sampler2D envMap, vec3 rayDirection, vec3 directionCamPerfect) {
      vec2 uvv = equirectUv( rayDirection );
      vec2 smoothUv = equirectUv( directionCamPerfect );
      return textureGrad(envMap, uvv, dFdx(correctMips ? smoothUv : uvv), dFdy(correctMips ? smoothUv : uvv));
    }
  #endif

  vec4 getTotalInternalReflection() {
    vec2 uv = gl_FragCoord.xy / resolution;
    vec3 directionCamPerfect = (projectionMatrixInverse * vec4(uv * 2.0 - 1.0, 0.0, 1.0)).xyz;
    directionCamPerfect = (viewMatrixInverse * vec4(directionCamPerfect, 0.0)).xyz;
    directionCamPerfect = normalize(directionCamPerfect);
    vec3 normal = vNormalInverse;
    vec3 rayOrigin = cameraPosition;
    vec3 rayDirection = normalize(vWorldPosition - cameraPosition);
    vec3 finalColor;
    #ifdef CHROMATIC_ABERRATIONS
      vec3 rayDirectionG = totalInternalReflection(rayOrigin, rayDirection, normal, max(ior, 1.0), vModelMatrixInverse);
      #ifdef FAST_CHROMA
        vec3 rayDirectionR = normalize(rayDirectionG + 1.0 * vec3(aberrationStrength / 2.0));
        vec3 rayDirectionB = normalize(rayDirectionG - 1.0 * vec3(aberrationStrength / 2.0));
      #else
        vec3 rayDirectionR = totalInternalReflection(rayOrigin, rayDirection, normal, max(ior * (1.0 - aberrationStrength), 1.0), vModelMatrixInverse);
        vec3 rayDirectionB = totalInternalReflection(rayOrigin, rayDirection, normal, max(ior * (1.0 + aberrationStrength), 1.0), vModelMatrixInverse);
      #endif
      float finalColorR = textureGradient(envMap, rayDirectionR, directionCamPerfect).r;
      float finalColorG = textureGradient(envMap, rayDirectionG, directionCamPerfect).g;
      float finalColorB = textureGradient(envMap, rayDirectionB, directionCamPerfect).b;
      finalColor = vec3(finalColorR, finalColorG, finalColorB) * envMapIntensity;
    //   finalColor = vec3(uv, 0.);
    // finalColor = texture2D(envMap, uv).xyz;
    // finalColor = vec3(1., 0., 0.);
    #else
      vec3 originalRayDir = rayDirection;
      rayDirection = totalInternalReflection(rayOrigin, rayDirection, normal, max(ior, 1.0), vModelMatrixInverse);
      #ifdef USE_TRANSMISSION
      if (dot(rayDirection, originalRayDir) > 0.75) {
    //   if (true) {
        vec4 reprojected = projectionMatrix * viewMatrix * vec4(cameraPosition + rayDirection, 1.0);
        vec2 uvv = (reprojected / reprojected.w).xy;
        uvv = vec2(uvv.x, uvv.y) * .5 + .5;
        vec2 smoothUv = equirectUv( directionCamPerfect );
        finalColor = textureGrad(transmissionSamplerMap, uvv, dFdx(correctMips ? smoothUv : uvv), dFdy(correctMips ? smoothUv : uvv)).xyz;
        // finalColor = texture2D(transmissionSamplerMap, uvv).xyz;
      }
      else {
        finalColor = textureGradient(envMap, rayDirection, directionCamPerfect).rgb * envMapIntensity;
      }
      #else
      //finalColor = textureGradient(envMap, rayDirection, directionCamPerfect).rgb * envMapIntensity;
      finalColor = textureCubeUV(envMap, rayDirection, roughness).rgb * envMapIntensity;

      #endif
    #endif

    finalColor *= diffuse;
    #ifdef USE_INSTANCING_COLOR
      finalColor *= vInstanceColor;
    #endif

    vec3 viewDirection = normalize(vWorldPosition - cameraPosition);
    float nFresnel = fresnelFunc(viewDirection, normal) * fresnel;
    return vec4(mix(finalColor, vec3(1.0), nFresnel), 1.0);
  }`;

class MeshGemMaterial extends THREE.MeshPhysicalMaterial
{
    constructor(cfg)
    {
        super();

        this.type = 'MeshPhysicalMaterial';
        this._bounces = 7;
        this._correctMips = true;
        this._aberrationStrength = 0.01;
        this._fresnel = 0.1;
        this._bvh = new MeshBVHUniformStruct();
        // this._color = new THREE.Color("white");
        this._resolution = new THREE.Vector2();

        this.uniforms = {
            envMapD: { value: null },
            ior: { value: 2 },
            bounces: { value: 7 },
            correctMips: { value: true },
            aberrationStrength: { value: 0.01 },
            fresnel: { value: 0.1 },
            bvh: { value: this._bvh },
            // color: { value: this._color },
            resolution: { value: this._resolution },
            viewMatrixInverse: { value: new THREE.Matrix4() },
            projectionMatrixInverse: { value: new THREE.Matrix4() }
        };
        this.setValues(cfg);
    }

    onBeforeCompile(shader)
    {
        shader.uniforms = {
            ...shader.uniforms,
            ...this.uniforms
        };

        // console.log(shader.uniforms);

        shader.vertexShader = shader.vertexShader.replace(
            "#include <clipping_planes_pars_vertex>",
            `#include <clipping_planes_pars_vertex>
            ${vertexShaderVaryings}`
        );

        shader.vertexShader = shader.vertexShader.replace(
            "#include <begin_vertex>",
            `#include <begin_vertex>
            ${vertexShaderAssignments}`
        );

        shader.fragmentShader = shader.fragmentShader.replace(
            "#include <clipping_planes_pars_fragment>",
            `#include <clipping_planes_pars_fragment>
            ${fragmentShader}`
        );

        shader.fragmentShader = shader.fragmentShader.replace(
            "vec3 outgoingLight = totalDiffuse + totalSpecular + totalEmissiveRadiance;",
            `vec4 internalReflection = getTotalInternalReflection();

            // #ifdef USE_TRANSMISSION
                // internalReflection.xyz = mix( internalReflection.xyz, transmitted.rgb, material.transmission );
            // #endif
            // vec3 outgoingLight = internalReflection.xyz;
            // totalDiffuse = mix(totalDiffuse, internalReflection.xyz, ior);
            vec3 outgoingLight = totalSpecular + totalEmissiveRadiance + internalReflection.xyz;`
        );

        // console.log(shader.vertexShader)
    }

    get ior()
    {
        return this._ior;
    }

    set ior(v)
    {
        this._ior = v;
        if (this.uniforms)
        {
            this.uniforms.ior.value = v;

        }
    }

    get bounces()
    {
        return this._bounces;
    }

    set bounces(v)
    {
        this._bounces = v;
        this.uniforms.bounces.value = v;
    }

    get correctMips()
    {
        return this._correctMips;
    }

    set correctMips(v)
    {
        this._correctMips = v;
        this.uniforms.correctMips.value = v;
    }

    get aberrationStrength()
    {
        return this._aberrationStrength;
    }

    set aberrationStrength(v)
    {
        this._aberrationStrength = v;
        this.uniforms.aberrationStrength.value = v;
    }

    get fresnel()
    {
        return this.__fresnel;
    }

    set fresnel(v)
    {
        this._fresnel = v;
        this.uniforms.fresnel.value = v;
    }

    get bvh()
    {
        return this._bvh;
    }

    set bvh(v)
    {
        this._bvh = v;
        this.uniforms.bvh.value = v;
    }

    // get color()
    // {
    //     return this._color;
    // }

    // set color(v)
    // {
    //     this._color = v;
    //     if (this.uniforms)
    //     {
    //         this.uniforms.color.value = v;
    //     }
    // }

    get resolution()
    {
        return this._resolution;
    }

    set resolution(v)
    {
        this._resolution = v;
        this.uniforms.resolution.value = v;
    }

    get envMapD()
    {
        return this.uniforms.envMapD;
    }

    set envMapD(v)
    {
        if (this.uniforms)
        {
            this.uniforms.envMapD.value = v;
        }
    }

    // TODO: implement copy method.
}

export default MeshGemMaterial;
